import React, { useContext, useEffect } from 'react';
import './stylesheet.scss';
import { useHistory, useLocation } from 'react-router-dom';
import { getAppKey } from 'common/utils';
import { FileSystemContext } from 'contexts';
import { Shortcut } from 'components';

function Desktop() {
  const [rootDir, refreshRootDir] = useContext(FileSystemContext);
  const desktopDir = rootDir.getDesktopDir();
  const apps = rootDir.getApps();

  const history = useHistory();
  const location = useLocation();
  const currentUrl = location.pathname;

  useEffect(() => {
    apps && apps.forEach(app => {
      const focused = getAppKey(currentUrl) === app.key;
      if (focused) {
        app.lastUrl = currentUrl;
        if (!app.opened) {
          app.opened = true;
          app.instance++;
        }
        if (!app.focused) {
          app.zIndex = Math.max(...apps.map(app => app.zIndex)) + 1;
        }
      }
      app.focused = focused;
    });
    refreshRootDir();
  }, [currentUrl]);

  useEffect(() => {
    apps && apps.forEach(app => {
      if (app.closing) {
        setTimeout(() => {
          app.closing = false;
          app.opened = false;
          refreshRootDir();
        }, 200);
      }
    });
  });

  return (
    <div className="Desktop" style={desktopDir && { backgroundImage: `url(${desktopDir.wallpaper})` }}
         onMouseDown={() => {
           if (currentUrl !== '/') history.push('/');
         }}>
      <a className="github-corner" href="https://github.com/parkjs814/parkjs814.github.io"
         aria-label="View source on Github">
        <svg width="80" height="80" viewBox="0 0 250 250" aria-hidden="true">
          <path d="M0,0 L115,115 L130,115 L142,142 L250,250 L250,0 Z"/>
          <path className="octo-arm" fill="currentColor" style={{ transformOrigin: '130px 106px' }}
                d="M128.3,109.0 C113.8,99.7 119.0,89.6 119.0,89.6 C122.0,82.7 120.5,78.6 120.5,78.6 C119.2,72.0 123.4,76.3 123.4,76.3 C127.3,80.9 125.5,87.3 125.5,87.3 C122.9,97.6 130.6,101.9 134.4,103.2"/>
          <path className="octo-body" fill="currentColor"
                d="M115.0,115.0 C114.9,115.1 118.7,116.5 119.8,115.4 L133.7,101.6 C136.9,99.2 139.9,98.4 142.2,98.6 C133.8,88.0 127.5,74.4 143.8,58.0 C148.5,53.4 154.0,51.2 159.7,51.0 C160.3,49.4 163.2,43.6 171.4,40.1 C171.4,40.1 176.1,42.5 178.8,56.2 C183.1,58.6 187.2,61.8 190.9,65.4 C194.5,69.0 197.7,73.2 200.1,77.6 C213.8,80.2 216.3,84.9 216.3,84.9 C212.7,93.1 206.9,96.0 205.4,96.6 C205.1,102.4 203.0,107.8 198.3,112.5 C181.9,128.9 168.3,122.5 157.7,114.1 C157.9,116.9 156.7,120.9 152.7,124.9 L141.0,136.5 C139.8,137.7 141.6,141.9 141.8,141.8 Z"/>
        </svg>
      </a>
      <div className="app-container">
        {
          desktopDir && desktopDir.children.map(child => (
            <Shortcut key={child.key} desktop target={child}/>
          ))
        }
      </div>
      <div className="window-container">
        {
          apps && apps.filter(app => app.opened).map(app => (
            <app.WindowComponent key={app.key}
                                 app={app}
                                 onUpdate={patch => {
                                   Object.assign(app, patch);
                                   refreshRootDir();
                                 }}/>
          ))
        }
      </div>
    </div>
  );
}

export default Desktop;
